//** Product Details Media Query START **//
// REUSABLE REVIEW MIXIN FROM MEDIA QUERIES
@mixin reviewXSSM {
  #review {
    .title {
      @include title(3.5rem, 0, 3rem);
      letter-spacing: 0.5rem;
    }
    .review {
      &__item__head__username,
      &__item__head__date {
        font-size: 1.75rem;
      }

      &__item__body__heading {
        font-size: 1.25rem;
      }
    }
  }
}

@mixin ProductDetailsXS {
  #detail {
    .title {
      padding-top: 5rem;
    }
    .detail {
      grid-template-columns: repeat(1, 1fr);

      &__img {
        margin-bottom: 2rem;
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(2, 350px);
        grid-gap: 1rem;
      }

      &__content {
        &__title {
          @include title(0, 0, 2rem);
          .price {
            font-size: 1.75rem;
          }
        }

        &__description {
          font-size: 2rem;
        }
      }
    }
  }

  // Section - Review
  @include reviewXSSM;
  // No Changes In Review Form
}

